import 'vue-awesome/icons/list';
import 'vue-awesome/icons/search';
import Icon from 'vue-awesome/components/Icon'
export default {
  name: 'appHeader',
  data() {
    return {}
  },
  components: {
    Icon
  },
  methods: {
    clickMenu: function () {
      this.$emit('click');
    },
    onSearch: function () {
      this.$router.push('search');
    }
  }
}



