
import XHeader from 'vux/src/components/x-header/index.vue'

import { isWechat } from '../libs/utils';
import loading from '../images/loading';
import moment from 'moment';
export default {
name: 'product-detail',
components: { XHeader },
data() {
return {
img: { product_image: loading }
}
},
mounted: function () {
var pid = this.$route.params.id;
this.load(pid);
this.$router.beforeEach((to, from, next) => {
next();
});
},
computed: {
isWechat: function () {
return isWechat();
}
},
methods: {
load(id) {
let vm = this;
fetch(`/api/web/product/${id}`).then(res => (res.json())).then(({ data }) => {
vm.$set(vm, 'img', data);
});
},
goBack() {
history.length > 1
? (this.$router.go(-1))
: (this.$router.replace('/'));
},
prehandlePrice(price) {
return Number(price) ? Number(price).toFixed(1) : '0.0';
},
prep(date) {
return moment(date).format('YYYY-MM-DD');
},
showCommand(command, action) {
this.$vux.alert.show({
title: '淘口令领券',
content: `复制以下淘口令后打开淘宝App即可${action}啦！\n${command}`,
onShow() {
},
onHide() {
}
});
}
}
}



