
import Drawer from 'vux/src/components/drawer/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
  name: 'KDrawer',
  components: { Drawer, XHeader },
  props: ['show', 'title', 'placement'],
  methods: {
    toggleDrawer: function () {
      this.$emit('toggle');
    }
  }
}



